@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@400;700&display=swap);
:root{--BrightBlue: hsl(220, 98%, 61%);--CheckBackground: linear-gradient( 0deg, hsl(192, 100%, 67%) 0%, hsl(280, 87%, 65%) 100% );--VeryLightGray: hsl(0, 0%, 98%);--VeryLightGrayishBlue: hsl(236, 33%, 92%);--LightGrayishBlue: hsl(233, 11%, 84%);--DarkGrayishBlue: hsl(236, 9%, 61%);--VeryDarkGrayishBlue: hsl(235, 19%, 35%);--VeryDarkBlue: hsl(235, 21%, 11%);--VeryDarkDesaturatedBlue: hsl(235, 24%, 19%);--LightGrayishBlue: hsl(234, 39%, 85%);--LightGrayishBlue--hover: hsl(236, 33%, 92%);--DarkGrayishBlue: hsl(234, 11%, 52%);--VeryDarkGrayishBlue: hsl(233, 14%, 35%);--VeryDarkGrayishBlue: hsl(237, 14%, 26%);--radius: 0.3rem;--box-shadow: 0 0 2rem 1rem #22222211, 0 0 0.5rem 0rem #22222233;--min-list-height: 25rem;--transition: 1s all ease-in-out}*,*::after,*::before{box-sizing:border-box;padding:0;margin:0;transition:1s all ease-in-out;transition:var(--transition)}body{font-size:17px;font-weight:400}body *{font-family:"Josefin Sans",sans-serif}body.dark{background-color:hsl(235, 21%, 11%);background-color:var(--VeryDarkBlue)}.container{width:90%;max-width:650px;margin:0 auto}::-webkit-scrollbar{width:7px}::-webkit-scrollbar-track{background:#f1f1f1}::-webkit-scrollbar-thumb{background:linear-gradient( 0deg, hsl(192, 100%, 67%) 0%, hsl(280, 87%, 65%) 100% );background:var(--CheckBackground);border-radius:1rem}::-webkit-scrollbar-thumb:hover{background:#888}.dark ::-webkit-scrollbar-track{background:hsl(235, 24%, 19%);background:var(--VeryDarkDesaturatedBlue)}.drag{text-align:center;font-size:1.3rem;color:hsl(234, 11%, 52%);color:var(--DarkGrayishBlue)}
.header{min-height:18rem;background-image:url(/todo-react/static/media/bg-desktop-light.3508d620.jpg);background-position:top;background-size:cover;background-repeat:no-repeat;background-color:#222;padding-top:5rem;font-family:"Josefin Sans",sans-serif;color:var(--DarkGrayishBlue)}.header .todo{width:100%;display:flex;justify-content:space-between}.header .todo .todo-text{color:#fff;text-transform:uppercase;letter-spacing:1rem}.header .create{width:100%;background-color:#fff;padding:1rem;margin-top:3rem;border-radius:var(--radius);display:flex;align-items:center;grid-gap:.6rem;gap:.6rem}.header .create .check-circle{flex-grow:0;flex-shrink:0;display:inline-block;width:1.2rem;height:1.2rem;background-color:transparent;border-radius:50%;border:1px solid var(--LightGrayishBlue)}.header .create .add-task-icon{border-radius:50%;border:1px solid var(--VeryDarkGrayishBlue);font-size:1rem;-webkit-transform:scale(1.2);transform:scale(1.2);flex-grow:0;flex-shrink:0;width:1.2rem;height:1.2rem;color:var(--VeryDarkGrayishBlue);display:flex;align-items:center;justify-content:center;transition:.3s all ease-in-out}.header .create .add-task-icon:hover{-webkit-transform:scale(1.6);transform:scale(1.6);cursor:pointer;background-color:var(--LightGrayishBlue--hover);border-color:var(--LightGrayishBlue--hover);color:var(--VeryDarkGrayishBlue);transition:.3s all ease-in-out}.header .create input{border:none;height:-webkit-max-content;height:max-content;width:100%;background-color:transparent}.header .create input:focus{outline:none}.dark .header{background-image:url(/todo-react/static/media/bg-desktop-dark.73e47dbb.jpg);background-color:#222;color:var(--DarkGrayishBlue)}.dark .header .todo .todo-text{color:#fff}.dark .header .create{background-color:var(--VeryDarkDesaturatedBlue)}.dark .header .create input{color:var(--LightGrayishBlue--hover)}.dark .header .create input:focus{outline:none}.dark .header .create .check-circle{border:1px solid var(--VeryDarkGrayishBlue)}.dark .header .create .add-task-icon{color:#eee;border-color:#eee}.dark .header .create .add-task-icon:hover{color:var(--VeryDarkDesaturatedBlue)}
.list-conainer{border-radius:var(--radius);background-color:var(--VeryLightGray);margin-top:-3rem;box-shadow:var(--box-shadow);overflow:hidden;height:var(--min-list-height);transition:var(--transition);transition:none}.list-conainer *{transition:none}.list-conainer .list{transition:none;height:calc(100% - 58px);overflow-y:auto;transition:var(--transition)}.empty-list{min-height:var(--min-list-height);display:flex;align-items:center;justify-content:center;flex-direction:column;background-color:var(--VeryLightGray);margin-top:-3rem;border-radius:var(--radius);overflow-y:hidden;box-shadow:var(--box-shadow);grid-gap:1.5rem;gap:1.5rem}.empty-list .text{color:var(--DarkGrayishBlue)}.empty-list .icon-container{width:7rem;height:7rem;background:var(--CheckBackground);border-radius:50%;display:flex;align-items:center;justify-content:center;opacity:.7}.empty-list .icon-container .icon{color:var(--VeryLightGray);font-size:4rem}.list-conainer,.empty-list{position:relative}.list-conainer .control,.empty-list .control{background-color:var(--VeryLightGray);position:absolute;bottom:0;width:100%;display:flex;padding:1rem;color:var(--DarkGrayishBlue);justify-content:space-between;align-items:center}.list-conainer .control .clear:hover,.empty-list .control .clear:hover{cursor:pointer}.filter{margin-top:1.5rem;margin-bottom:3rem;border-radius:var(--radius);overflow-y:hidden;box-shadow:var(--box-shadow)}.filter ul{width:100%;display:flex;list-style:none;justify-content:center;align-items:center;grid-gap:2rem;gap:2rem;padding:1.1rem;color:var(--DarkGrayishBlue);font-weight:bold}.filter ul li:hover{cursor:pointer}.filter ul li.active{color:var(--BrightBlue)}.mobile-only{transition:var(--transition);width:100%;overflow:hidden;margin-left:auto;margin-right:auto;-webkit-transform:translateY(0);transform:translateY(0)}.desktop-only{transition:var(--transition);width:0;height:0;padding:0;margin:0;box-shadow:none;overflow:hidden;-webkit-transform:translateY(5rem);transform:translateY(5rem)}.dark .list-conainer{background-color:var(--VeryDarkDesaturatedBlue)}.dark .list-conainer .control,.dark .empty-list .control{background-color:var(--VeryDarkDesaturatedBlue);color:var(--DarkGrayishBlue)}.dark .empty-list{min-height:var(--min-list-height);background-color:var(--VeryDarkDesaturatedBlue)}.dark .empty-list .text{color:var(--DarkGrayishBlue)}.dark .empty-list .icon-container{background:var(--CheckBackground)}.dark .empty-list .icon-container .icon{color:var(--VeryLightGray)}.dark .filter{box-shadow:var(--box-shadow)}.dark .filter ul{background-color:var(--VeryDarkDesaturatedBlue);color:var(--DarkGrayishBlue)}.dark .filter ul li.active{color:var(--BrightBlue)}@media only screen and (min-width: 670px){.mobile-only{box-shadow:none;-webkit-transform:translateY(-5rem);transform:translateY(-5rem);position:relative;z-index:-1}.desktop-only{display:flex;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;padding:0;box-shadow:none;margin:0;-webkit-transform:translateY(0);transform:translateY(0)}.list-conainer .control,.empty-list .control{padding:0 1rem}}
.task{width:100%;background-color:var(--VeryLightGray);color:var(--DarkGrayishBlue);padding:1rem;display:flex;align-items:center;grid-gap:.6rem;gap:.6rem;border-bottom:1px solid var(--LightGrayishBlue)}.task .check-circle{width:1.2rem;height:1.2rem;flex-grow:0;flex-shrink:0;background-color:transparent;border-radius:50%;border:1px solid var(--LightGrayishBlue);display:flex;align-items:center;justify-content:center}.task .check-circle:hover{cursor:pointer}.task .check-circle.checked{background-image:var(--CheckBackground);background-position:center;background-repeat:no-repeat}.task .close{height:-webkit-min-content;height:min-content;width:-webkit-min-content;width:min-content;margin-left:auto}.task .close:hover{cursor:pointer}.task .text{transition:.5s all ease-in-out;width:-webkit-max-content;width:max-content}.task .text::after{transition:.5s all ease-in-out;display:block;content:"";width:0%;height:0;border-bottom:1px solid var(--LightGrayishBlue);-webkit-transform:translateY(-0.5rem);transform:translateY(-0.5rem)}.task .text.checked{color:var(--LightGrayishBlue)}.task .text.checked::after{transition:.5s all ease-in-out;width:100%}.dark .task{background-color:var(--VeryDarkDesaturatedBlue);color:var(--LightGrayishBlue);border-bottom:1px solid var(--VeryDarkGrayishBlue)}.dark .task .check-circle{border:1px solid var(--VeryDarkGrayishBlue)}.dark .task .text::after{border-bottom:1px solid var(--LightGrayishBlue)}.dark .task .text.checked{color:var(--DarkGrayishBlue)}
